














































import { Component, Vue } from 'vue-property-decorator';
import codes from '@/utils/codes/install';
import Clipboard from '@/utils/clipboard';
import dtFoot from '@/components/Foot.vue';

@Component({
  components: {
    dtFoot,
  },
})
export default class VInstall extends Vue {
  private codes = codes;

  private copied: boolean = false;
  private alertMessage = 'Code successfully copied';
  private alertType = 'primary';

  private clipboard(text: string) {
    Clipboard(text)
      .then((r) => {
        this.copied = true;
        this.alertMessage = 'Code successfully copied';
        this.alertType = 'success';
        setTimeout(() => {
          this.copied = false;
        }, 1500);
      })
      .catch((e) => {
        this.alertMessage = 'Code not copied';
        this.alertType = 'danger';
      });
  }
}
